import { emit } from '@nextcloud/event-bus';
var token = document.getElementsByTagName('head')[0].getAttribute('data-requesttoken');
/**
 * @returns {string}
 */

export var getToken = function getToken() {
  return token;
};
/**
 * @param {String} newToken new token
 */

export var setToken = function setToken(newToken) {
  token = newToken;
  emit('csrf-token-update', {
    token: token
  });
};